.company-container {
  text-align: center;
}

.company-container > img {
  max-width: 100%;
  margin: auto;
  margin-bottom: 64px;
}

.company-container > h2 {
  margin: 0px;
}

.company-container > p {
  margin: 0px;
}

.project-details-container {
  margin-top: 61px;
  display: flex;
  flex-direction: column;
  gap: 60px;
}

.project-details-flex-item {
  flex-grow: 1;
  flex-basis: 0;
}

@media only screen and (min-width: 640px) {
  .project-details-container {
    flex-direction: row;
    flex-wrap: nowrap;
  }
}
